import React, {Component} from 'react';
import ReactMarkdown from 'react-markdown';
import {Redirect} from 'react-router';
import Header from './Header';
import {ossUrl, iconClass, getStoryMap} from '../config/constant';

class Story extends Component {
  
  constructor(props) {
    super(props);

    this.state = {
      mainKey: props.match.params.mainKey,
      subKey: props.match.params.subKey,
      id: props.match.params.id,
      mdContent: '',
      storyMap: getStoryMap(props.match.params.mainKey)
    };
  }

  componentDidMount() {
    const mdPath = ossUrl+'/content/'+this.state.mainKey+'/'+this.state.subKey+'/'+this.state.id+'.md';
    fetch(mdPath).then((response) => (response.ok?response.text():null))
    .then((text) => {
      this.setState({ ...this.state, mdContent: text })
    });
    // const path = ossUrl+'/config/'+this.state.mainKey+'.json';
    // fetch(path).then((response) => (response.ok?response.json():null))
    // .then((json) => {
    //   this.setState({ ...this.state, storyMap: json})
    // });
  }

  handleScrollTop() {
    window.scrollTo({
      left: 0,
      top: 0,
      behavior: 'smooth',
    });
  }

  render() {
    let {mainKey,subKey,id,mdContent,storyMap} = this.state;
    if(mdContent==null || storyMap==null){
      return <Redirect to="/404" />;
    }

    let deactivedItemClass = "list-group-item list-group-item-action";
    let activedItemClass = deactivedItemClass + " active";
    return (
      <div>
        <Header mainKey={mainKey}/>
        <div className="container storydiv" style={{marginTop:'40px'}}>
          <div className="row">
            <div className="col-sm-8">
              <ReactMarkdown source={mdContent}/>
              <br/>
              <hr/>
              <div style={{textAlign:'center'}}>
                <button type="button" className="btn btn-light" onClick={this.handleScrollTop}>
                <i className={iconClass('top')}></i>&nbsp;<span>返回顶部</span></button>
              </div>
              <br/>
            </div>

            <div className="col-sm-4">
              <div className="list-group">
                {
                  (storyMap.hasOwnProperty(subKey) && storyMap[subKey].length>0)?
                  storyMap[subKey].map((story)=>(
                    <a href={"/s/"+mainKey+"/"+subKey+"/"+story.id+"/story.html"} key={story.id}
                    className={story.id===id?activedItemClass:deactivedItemClass}>
                    <i className={iconClass('list')}></i>&nbsp;<span>{story.title}</span></a>
                  )):''
                }
              </div>
            </div>
          </div>
          
        </div> 
      </div>
    )
  }
  
}

export default Story;
