import React, {Component} from 'react';
import {appTitle, mainTypeMap, iconClass} from '../config/constant';

class Home extends Component {

  componentDidMount() {
    document.body.style.backgroundSize = "cover";
    document.body.style.backgroundRepeat = "no-repeat";
    document.body.style.backgroundAttachment = 'fixed';

    let isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
    let imgUrl = isMobile?"url(/image/home-mobile.jpg)":"url(/image/home-desktop.jpg)";
    document.body.style.backgroundImage = imgUrl;
  }

  render() {
    return (
      <div className="container">
        <h1 className="display-4 home-title">{appTitle}</h1>
        <ul className="nav justify-content-center">
        {
          Object.keys(mainTypeMap).map((key) => (
            <li className="nav-item" key={key}>
              <a className="nav-link home-link" href={"/l/"+key+"/list.html"}>
              <i className={iconClass(key)}></i>&nbsp;<span>{mainTypeMap[key]}</span></a>
            </li>
          ))
        }
        </ul>
      </div>
    );
  }
}



export default Home;
