import React, {Component} from 'react';
import {Redirect} from 'react-router';
import {subTypeMap, ossUrl, iconClass, getStoryMap} from '../config/constant';
import Header from './Header';

class List extends Component {
  
  constructor(props) {
    super(props);

    this.state = {
      mainKey: props.match.params.mainKey,
      storyMap: getStoryMap(props.match.params.mainKey)
    };
  }

  componentDidMount() {
    // if(subTypeMap.hasOwnProperty(this.state.mainKey)){
    //   const path = ossUrl+'/config/'+this.state.mainKey+'.json';
    //   fetch(path).then((response) => (response.ok?response.json():{}))
    //   .then((json) => {
    //     this.setState({ ...this.state, storyMap: json})
    //   })
    // }
  }

  render() {
    if(!subTypeMap.hasOwnProperty(this.state.mainKey)){
      return <Redirect to="/404" />;
    }

    const subKeyList = Object.keys(subTypeMap[this.state.mainKey]);

    return (
      <div>
        <Header mainKey={this.state.mainKey}/>
        <div className="container">
          {
            (subKeyList.length>0)?
            subKeyList.map((subKey) =>(
              (this.state.storyMap.hasOwnProperty(subKey) && this.state.storyMap[subKey].length>0)?
              <div key={subKey}>
                <h6 style={{marginLeft:'-5px',marginTop:'40px',marginBottom:'0px'}}>
                <i className={iconClass(subKey)}></i>&nbsp;<span>{subTypeMap[this.state.mainKey][subKey]}</span></h6>
                <div className="row">
                  {
                    this.state.storyMap[subKey].map((story)=>(
                      <div className="col-sm-4" key={story.id} style={{padding:'10px'}}>
                        <div className="card shadow">
                          {story.hasOwnProperty('cover')?<img src={ossUrl+story.cover} style={{width:'100%'}} alt="..."></img>:''}
                          <div className="card-body">
                            <p className="card-text" style={{marginBottom:'0px'}}>{story.title}</p>
                            <p className="text-right" style={{marginTop:'-24px',marginBottom:'0px'}}>
                              <a href={"/s/"+this.state.mainKey+"/"+subKey+"/"+story.id+"/story.html"} className="card-link text-right">
                              <i className={iconClass('enter')}></i></a>
                            </p>
                            
                          </div>
                        </div>
                      </div>
                    ))
                  }
                </div>
              </div>:''
            )):<p className="text-center" style={{marginTop:'100px'}}>内容准备中，敬请期待......</p>
          }
          <br/>
        </div> 
      </div>
    )
  }
  
}

export default List;
